"use client"

import { useState, useEffect } from "react"
import { Bell, CheckCheck, Plus, Trash2 } from "lucide-react"
import { useStore } from "@/store"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { RoleUser } from "@/types/enums"
import type { Notification } from "@/types/notification"
import { useToast } from "@/hooks/use-toast"
import { cn } from "@/lib/utils"

export default function NotificationsPage() {
  const { toast } = useToast()
  const { user } = useStore.getState().profile

  const [notifications, setNotifications] = useState<Notification[]>([])
  const [loading, setLoading] = useState(true)
  const [activeTab, setActiveTab] = useState<string>("all")

  const loadNotifications = async () => {
    if (!user) return

    try {
      setLoading(true)
      const data = await fetchNotifications(user.role as RoleUser, user.id)
      setNotifications(data)
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to load notifications",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    loadNotifications()
  }, [user, toast])

  const handleViewNotification = (notification: Notification) => {
  }

  const handleMarkAllAsRead = async () => {
    if (!user) return

    try {
      await markAllNotificationsAsRead(user.role as RoleUser, user.id)
      toast({
        title: "Success",
        description: "All notifications marked as read",
      })
      loadNotifications()
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to mark notifications as read",
        variant: "destructive",
      })
    }
  }

  const handleDeleteNotification = async (id: number) => {
    try {
      await deleteNotification(id)
      setNotifications(notifications.filter((n) => n.id !== id))
      toast({
        title: "Success",
        description: "Notification deleted",
      })
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to delete notification",
        variant: "destructive",
      })
    }
  }

  const handleCreateNotification = () => {
  }

  const filteredNotifications = notifications.filter((notification) => {
    if (activeTab === "unread" && notification.readStatus) return false
    if (activeTab === "read" && !notification.readStatus) return false

    return true
  })

  const unreadCount = notifications.filter((n) => !n.readStatus).length
  const readCount = notifications.filter((n) => n.readStatus).length

  const canCreateNotifications = user?.role === RoleUser.ADMIN || user?.role === RoleUser.STAFF

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    const now = new Date()
    const diffMs = now.getTime() - date.getTime()
    const diffMins = Math.round(diffMs / 60000)
    const diffHours = Math.round(diffMs / 3600000)
    const diffDays = Math.round(diffMs / 86400000)

    if (diffMins < 60) {
      return `${diffMins} minute${diffMins !== 1 ? "s" : ""} ago`
    } else if (diffHours < 24) {
      return `${diffHours} hour${diffHours !== 1 ? "s" : ""} ago`
    } else if (diffDays < 7) {
      return `${diffDays} day${diffDays !== 1 ? "s" : ""} ago`
    } else {
      return date.toLocaleDateString()
    }
  }

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold">Notifications</h1>
        <div className="flex gap-2">
          {unreadCount > 0 && (
            <Button variant="outline" onClick={handleMarkAllAsRead}>
              <CheckCheck className="mr-2 h-4 w-4" />
              Mark all as read
            </Button>
          )}
          {canCreateNotifications && (
            <Button onClick={handleCreateNotification}>
              <Plus className="mr-2 h-4 w-4" />
              Create Notification
            </Button>
          )}
        </div>
      </div>

      <div className="grid gap-4 md:grid-cols-3">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Total Notifications</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center">
              <Bell className="mr-2 h-5 w-5 text-blue-600" />
              <div className="text-2xl font-bold">{notifications.length}</div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Unread Notifications</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center">
              <Bell className="mr-2 h-5 w-5 text-red-600" />
              <div className="text-2xl font-bold">{unreadCount}</div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Read Notifications</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center">
              <Bell className="mr-2 h-5 w-5 text-green-600" />
              <div className="text-2xl font-bold">{readCount}</div>
            </div>
          </CardContent>
        </Card>
      </div>

      <div className="flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
        <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full sm:w-auto">
          <TabsList>
            <TabsTrigger value="all">All</TabsTrigger>
            <TabsTrigger value="unread">Unread</TabsTrigger>
            <TabsTrigger value="read">Read</TabsTrigger>
          </TabsList>
        </Tabs>
      </div>

      <Card>
        <CardContent className="p-0">
          {loading ? (
            <div className="flex h-40 items-center justify-center">
              <p>Loading notifications...</p>
            </div>
          ) : filteredNotifications.length === 0 ? (
            <div className="flex h-40 flex-col items-center justify-center gap-2">
              <Bell className="h-8 w-8 text-muted-foreground" />
              <p className="text-muted-foreground">No notifications found</p>
            </div>
          ) : (
            <div className="divide-y">
              {filteredNotifications.map((notification) => (
                <div
                  key={notification.id}
                  className={cn(
                    "cursor-pointer p-4 transition-colors hover:bg-muted/50",
                    !notification.readStatus && "bg-blue-50",
                  )}
                  onClick={() => handleViewNotification(notification)}
                >
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                      <h3 className={cn("text-base", !notification.readStatus && "font-medium")}>
                        {notification.user.fullname}
                      </h3>
                    </div>
                    <Button
                      variant="ghost"
                      size="icon"
                      className="h-8 w-8 text-muted-foreground hover:text-destructive"
                      onClick={(e) => {
                        e.stopPropagation()
                        handleDeleteNotification(notification.id)
                      }}
                    >
                      <Trash2 className="h-4 w-4" />
                      <span className="sr-only">Delete</span>
                    </Button>
                  </div>
                  <p className="mt-1 line-clamp-2 text-sm text-muted-foreground">{notification.messages}</p>
                  <div className="mt-2 flex items-center justify-between text-xs text-muted-foreground">
                    <span>From: {notification.user.fullname}</span>
                    <span>{formatDate(notification.createdAt)}</span>
                  </div>
                </div>
              ))}
            </div>
          )}
        </CardContent>
      </Card>

      {/* <CreateNotificationModal /> */}
    </div>
  )
}

